.headerWarp { position: fixed; top: 0; left: 0; width: 100%;
    header { position: relative;
        .logo { position: absolute; left: 50%; transform: translate(-50%); }
        .rowgroup1 {
            .row { display: flex; border: 3px solid #000; height: 56px;
                   padding-top: 32px;
                .col1 { align-self: center;
                    ul li { display: inline-block; padding-left: 15px; }
                    ul li a { font-size: 13px; }
                }
                .col2 { align-self: center; 
                    ul li { display: inline-block; padding-left: 15px; }
                }
            }
        }
        .rowgroup2 { background: #0f4b7e;
            .row { display: flex; border: 3px solid #000; height: 56px; color: #fff;
                .col3 { align-self: center;
                    ul li { display: inline-block; padding-left: 50px; }
                }
                .col4 { align-self: center; position: relative; left: 8%;
                    ul li { display: inline-block; }
                    ul li a { color: #dde6ed;
                        &:nth-of-type(2):before { content: 'ã…£'; }
                    }
                }
            }
        }
    }
}